const statusButton = document.querySelector('.statusBtn');
const request = require('request');


function getStatus() {
    request('https://www.githubstatus.com/', { json: true }, (err, res, body) => {
        let result = body.components;
        const status = document.querySelectorAll('.status__item');

        for (let i = 0; i < result.length; i += 1) {
            let statusName = status[i].querySelector('.status__name');
            statusName.textContent = `${result[i].name}`;

            let flag = status[i].querySelector('.status__flag');
            if (result[i].status !== 'operational') {
                flag.classList.add('error');
                flag.textContent = `${result[i].status}`;
            } else {
                flag.textContent = `${result[i].status}`;
            }
        }
    });
}

statusButton.addEventListener('click', getStatus);